import React, { useState } from "react";
import ReactPlayer from "react-player";
import { playMusicActionCreator } from "../../redux/player-reducer";
import track1 from "./../../assets/music/Mark Morgan - Khans Of The New California.mp3";
import track2 from "./../../assets/music/Lorn - PERFEKT DARK.mp3";
import track3 from "./../../assets/music/BLVCK CEILING - End of Time.mp3";
import track4 from "./../../assets/music/Burial - Pirates.mp3";
import track5 from "./../../assets/music/Natalie Merchant - San Andreas Fault.mp3";
import { connect, useDispatch, useSelector } from "react-redux";

/*const Music = (props) => {
  let [isPlaying, setPlaying] = useState(false);
  let [currentTrack, setCurrentTrack] = useState(0);
  let onPlayerClick = (url) => {
    playMusic(url);
    setPlaying(true);
    setCurrentTrack(props.url);
  };*/

const Music = () => {
  let isPlaying = useSelector((state) => state.player.isPlaying);
  let currentTrack = useSelector((state) => state.player.url);
  const dispatch = useDispatch();
  let onPlayerClick = (currentTrack) => {
    dispatch(playMusicActionCreator(currentTrack));
    console.log(currentTrack);
  };
  return (
    <div>
      <div>
        <span
          onClick={() => {
            onPlayerClick(track1);
          }}
        >
          Mark Morgan - Khans Of The New California
        </span>
      </div>
      <div>
        <span
          onClick={() => {
            onPlayerClick(track2);
          }}
        >
          Lorn - PERFEKT DARK
        </span>
      </div>
      <div>
        <span
          onClick={() => {
            onPlayerClick(track3);
          }}
        >
          BLVCK CEILING - End of Time
        </span>
      </div>
      <div>
        <span
          onClick={() => {
            onPlayerClick(track4);
          }}
        >
          Burial - Pirates
        </span>
      </div>
      <div>
        <span
          onClick={() => {
            onPlayerClick(track5);
          }}
        >
          Natalie Merchant - San Andreas Fault{" "}
        </span>
      </div>
      {isPlaying ? (
        <ReactPlayer
          url={currentTrack}
          controls={true}
          height="40px"
          width="30%"
          playing={true}
          config={{
            file: {
              tracks: [track1, track2, track3, track4, track5],
            },
          }}
        />
      ) : (
        <div></div>
      )}
    </div>
  );
};

let mapStateToProps = (state) => {
  return {
    url: state.player.url,
  };
};

export default connect(mapStateToProps)(Music);
